import { useEffect, useState } from "react";
import "./App.css";
import Title from "./Title.js";
import Form from "./Form.js";
import Info from "./Info.js";
import Suggestion from "./Suggestion.js";

export default function App() {
  const [name, setName] = useState("");
  const [data, setData] = useState("");

  useEffect(() => {
    const url = `https://api.artic.edu/api/v1/artworks/search?q=${name.toLowerCase()}&limit=1&fields=id,title,image_id`;
    fetch(url)
      .then((r) => r.json())
      .then((r) => setData(r))
      .catch((e) => setData(null));
  }, [name]);

  return (
    <div className="App">
      <Title title="What is in the Art Institute of Chicago?" />
      <Form setName={setName} />
      <Suggestion suggestion="Type in an artist or a title." />
      <Info data={data} name={name} />
    </div>
  );
}
