export default function Info({ name, data }) {
  const id = data?.data?.[0]?.image_id; // Covers data too
  return !id ? (
    <div> Sorry, {name} was not found. </div>
  ) : (
    <div>
      <img
        src={`https://www.artic.edu/iiif/2/${data.data[0].image_id}/full/843,/0/default.jpg`}
        alt="Artwork"
      />
    </div>
  );
}
