import { useState } from "react";

export default function Form({ setName }) {
  const [content, setContent] = useState("");
  return (
    <div id="form">
      <input
        onChange={(e) => {
          setContent(e.target.value);
        }}
      />
      <button
        onClick={() => {
          setName(content);
        }}
      >
        Find
      </button>
    </div>
  );
}
